.container {
    width: 80%;
    height: 110px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
}

.find {
    border-radius: 8px;
    border-color: rgb(59, 59, 59);
    background-color: rgb(59, 59, 59);
    color: rgb(197, 197, 197);
    width: 300px;
    height: 30px;
    font-size: 18px;
    font-weight: bold;
    font-family: Arial, Helvetica, sans-serif;
    margin-top: 48px;
    margin-left: 55px;
    margin-right: 20px;
}

.button {
    border-radius: 8px;
    border-color: rgb(27, 27, 27);
    background-color: rgb(27, 27, 27);
    box-shadow: 0 0 1px 3px rgba(59, 153, 252, .7);
    color: rgb(255, 255, 255);
    width: 100px;
    height: 25px;
    font-size: 15px;
    font-weight: bold;
    font-family: Arial, Helvetica, sans-serif;
    margin-top: 55px;
    margin-right: 20px;
}

@media all and (max-width: 1024px) {
    .container {
        display: grid;
        height: auto;
        width: 90%;
    }
    .find {
        margin-top: 20px;
        width: 100%;
        margin-left: 0px;
    }
    .button {
        width: 100%;
        height: 40px;
        margin-bottom: 0px;
        margin-top: 30px;
    }
}