body {
    background-color: rgb(27, 27, 27);
}
.ListStyle {
    background-color: rgb(42, 42, 42);
    border-radius: 5px;
    display: flex;
    height: 45px;
    font-size: 16px;
    margin-top: auto;
    margin-bottom: auto; 
    margin-left: auto;
    margin-right: auto; 
    width: 80%;
}

.Item {
    color: white;
    margin-left: 20px;
    font-family: Arial, Helvetica, sans-serif;
    text-align: right;
    margin-top: 12px;
    margin-bottom: auto;
}

.ItemLogin {
    color: white;
    margin-left: 20px;
    font-family: Arial, Helvetica, sans-serif;
    text-align: right;
    margin-top: auto;
    margin-bottom: auto;
}

@media all and (max-width: 1024px) {
    .ListStyle {
        width: 100%;
        margin-left: 0px;
    }
    .Item {
        margin-left: 13px;
        text-align: left;
        font-weight: bold;
    }
}