.container {
    width: 80%;
    height: auto;
    background-color: rgb(29, 29, 29);
    display: flex;
    margin-left: auto;
    margin-right: auto;
}

@media all and (max-width: 1024px) {
    .container {
        display: block;
        width: 90%;
        margin-left: 4px;
    }
}