.container {
    width: 80%;
    height: auto;
    margin-left: auto;
    margin-right: auto;
    display: flex;
}

.filterstyle {
    border-radius: 8px;
    cursor: pointer;
    border-color: rgb(27, 27, 27);
    background-color: rgb(27, 27, 27);
    color: rgb(197, 197, 197);
    width: 150px;
    height: 25px;
    font-size: 18px;
    font-weight: bold;
    font-family: Arial, Helvetica, sans-serif;
    margin-top: 20px;
    padding-left: 20px;
    margin-right: 20px;
}

.icon {
    font-size: 14px;
    margin-right: 10px;
}

@media all and (max-width: 1024px) {
    .container {
        width: 90%;
        margin-left: auto;
        margin-right: auto;
        height: auto;
        display: grid;
    }
    .filterstyle {
        margin-left: 0px;
    }
}